import { Component, OnInit } from '@angular/core';
import { TmService } from 'src/app/services/tm.service';
import {HttpErrorResponse} from '@angular/common/http';
import{Transactions} from '../../entity/Transactions'
import { Transaction } from 'src/app/entity/transaction';
import * as moment from 'moment';
import {DlDateTimePickerChange} from 'angular-bootstrap-datetimepicker';
import { format } from 'util';

declare var $:any;

@Component({
  selector: 'app-tm',
  templateUrl: './tm.component.html',
  styleUrls: ['./tm.component.css']
})

export class TmComponent implements OnInit {

  date:Date=new Date();
  settings={
    bigBanner:true,
    timePicker:true,
    format:'dd-MM-yyyy hh:mm a',
    defaultOpen:false,
    closeOnSelect:false
  };
  names:Array<(string)>=[];
  constructor(private _service:TmService) { 
  }
  transactions:Transactions;
  transaction:Transaction;
  displayData:boolean;
  from:Date=new Date();
  to:Date;
  
  
  ngOnInit() {

    this.displayData=false;

    $(document).ready(function(){    
      $(".searchButton").on("click", function(){    
          $.startLoader();
      });
      });
      $.closeLoader = function() {
        $(".overlay").hide();  
    };
    
    $.startLoader = function() {
        $(".overlay").show();  
        setTimeout(function(){ $.closeLoader }, 12000);
    };

    $.showDropdown = function() {
      $(".dropdwnListContainer").css("display","block");
      setTimeout(function(){ $.hideDropdown }, 12000);
  };

      $.hideDropdown = function() {
        $(".dropdwnListContainer").css("display","none");
      };
  }

  public onSubmit()
  { 

    this._service.getTransactionDetails(moment(this.from).format("YYYY-MM-DD HH:mm:ss"),moment(this.to).format("YYYY-MM-DD HH:mm:ss"),$("#namechar").val()).subscribe(
      response=>{
            this.transactions=this._service.transactions; 
            this.displayData=true;
            $.closeLoader();
            if( Object.keys(this.transactions).length==0)
            {
              alert("Please increase your date range");
            }

      },(error:HttpErrorResponse)=>{
        alert("Selected dates are not proper please reselect dates");
        $.closeLoader();
      if(error.error instanceof Error){
        console.log("Client side error in fetching transaction details");
      }
      else{
        console.log("Server side error in fetching transaction details");
       }
    });
  }

  onSearchChange(nameChar : any  ) { 
    $.startLoader();
    $.showDropdown();
    this._service.getNameList(nameChar).subscribe(response=>{
      this.names=response;
      $.closeLoader();    
      console.log("Details"+this.names);
    },
    (error:HttpErrorResponse)=>{
    if(error.error instanceof Error){
      console.log("Client side error");
    }
    else{
      console.log("Server side error");
     }
  });
  
}

public setInputTextvalue(id:string){
  $("#namechar").val(id);
  $(".dropdwnListContainer").css("display","none");
}

public removeName(){
  $(".dropdwnListContainer").css("display","none");
  $("#namechar").val("");
  return;
}


}
