import { Component, OnInit } from '@angular/core';
import{RequestDetailsService} from '../../services/request-details.service'

@Component({
  selector: 'app-request-details',
  templateUrl: './request-details.component.html',
  styleUrls: ['./request-details.component.css']
})
export class RequestDetailsComponent implements OnInit {

  labels:Array<any> = ["2XX", "4XX", "5XX"]
  stats:any = {};

  constructor(private healthMonitorService:RequestDetailsService) { }

  ngOnInit() {
    this.healthMonitorService.getGetReqDetailData().subscribe(result => {
      let resChartData: Array<any> = [];
      resChartData.push(result.statusOKCount);
      resChartData.push(result.status4XXCount);
      resChartData.push(result.status5XXCount);

      this.stats = {
        chartData: resChartData,
        totalCount: result.totalCount,
        maxTime: result.maxTime,
        minTime: result.minTime        
      }
    },error => {
      console.log('error fetching health data.');
      console.log(error);
    });
  }

}
