import { Injectable } from '@angular/core';
import {Http} from '@angular/http';
import { Transaction } from '../entity/transaction';
import { Observable } from 'rxjs';
import {map} from 'rxjs/operators';
import { Transactions } from '../entity/Transactions';

@Injectable({
  providedIn: 'root'
})
export class TmService {

  transactiontUrl="http://localhost:8081/client/tm?";
  nameListUrl="http://localhost:8081/client/name?";
  transactions:Transactions;

  constructor(private _http:Http) { }

  public getTransactionDetails(startDate:string,endtDate:string,name:string):any
  {
    console.log("URL>>>>"+this.transactiontUrl+"&startDate="+startDate+"&endtDate="+endtDate+"&name="+name);
    return this._http.get(this.transactiontUrl+"&startDate="+startDate+"&endtDate="+endtDate+"&name="+name).pipe(map(response=>{
      this.transactions=response.json();
      console.log(this.transactions);
      return this.transactions;
    },
    error=>
    {
      this.handleError(error);
    }));
  }

  public getNameList(nameChar:string):any
  {
    return this._http.get(this.nameListUrl+"&nameChar="+nameChar).pipe(map(response=>{
    return response.json();
    },
    error=>
    {
      this.handleError(error);
    }));
  }

  public handleError(error)
  {
    console.error("Error is "+ error);
    return Observable.throw(error ||"Internal server error");
  }
}
